        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售金额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      spumTableHeader: [{name: 'SPU', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存金额', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售金额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      shopqTableHeader: [{name: '店铺', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存数量', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售数量', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      dqTableHeader: [{name: '大类', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存数量', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售数量', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      spuqTableHeader: [{name: 'SPU', prop: 'type', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '库存数量', prop: 'ke', needOrder: true, order: '2', isPresent: false, needArrow: false, newLine: true},
        {name: '库存占比', prop: 'kb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '销售数量', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false, newLine: true},
        {name: '销售占比', prop: 'xb', needOrder: true, order: '0', isPresent: true, needArrow: false, newLine: true},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true}],
      zgsJgfxTableDataInit: [],
      zgsJgfxBarLineData: [],
      zgsJgfxTableData: {
        tableHeader: [],
        tableContent: []
      },
      zgsJgfxIndex: '00',
      zgsJgfxTabIndex: 0,
      zgsJgfxBtnIndex: 0,
      // 店铺数据
      //  店铺库存分析表格数据
      shopKcfxTableDataInit: [],
      shopKcfxTableData: {
        tableHeader: [{name: '店铺', prop: 'shop', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '数量', prop: 'qty', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '金额', prop: 'amt', needOrder: true, order: '2', isPresent: false, needArrow: false},
          {name: '控制差异率', prop: 'kzcy', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '周转天数', prop: 'zzts', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}],
        tableContent: [
          // {shop: '上海水星一店', qty: '2000', amt: '1600', kzcy: '55', zzts: '35.4', tb: '35.4', zb: '30.7'},
          // {shop: '上海水星二店', qty: '2400', amt: '1800', kzcy: '30', zzts: '-25.4', tb: '-25.4', zb: '20.7'},
          // {shop: '上海水星三店', qty: '2600', amt: '1400', kzcy: '30', zzts: '-55.4', tb: '-55.4', zb: '40.7'},
          // {shop: '上海水星四店', qty: '2800', amt: '1200', kzcy: '60', zzts: '65.4', tb: '65.4', zb: '50.7'}
        ]
      },
      shopQsfxLineData: [ // 趋势分析-店铺模拟的柱线图数据
        // {label: '1', zzts: '10', kzcy: '5', cy: '28'},
        // {label: '2', zzts: '20', kzcy: '15', cy: '34'},
        // {label: '3', zzts: '30', kzcy: '27', cy: '56'},
        // {label: '4', zzts: '40', kzcy: '38', cy: '34'},
        // {label: '5', zzts: '50', kzcy: '38', cy: '67'},
        // {label: '6', zzts: '55', kzcy: '48', cy: '23'}
      ],
      shopJgfxTableDataInit: [],
      shopJgfxBarLineData: [],
      shopJgfxTableData: {
        tableHeader: [],
        tableContent: []
      },
      shopJgfxIndex: '00',
      shopJgfxTabIndex: 0,
      shopJgfxBtnIndex: 0
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    selectedDwName () {
      return this.$store.state.selectedDwName
    }
  },
  methods: {
    tabsType ({activeValue, name, type}) {
      alert('你点击了' + activeValue + name + type)
    },
    changeTypeList ({activeTypeName, index, type}) {
      if (type === 'stock-zgs-jgfx') {
        this.zgsJgfxTabIndex = index
        this.dealZgsJgfxData()
      } else if (type === 'stock-shop-jgfx') {
        this.shopJgfxTabIndex = index
        this.dealShopJgfxData()
      }
    },
    changTabsType ({activeValue, index, type}) {
      if (type === 'stock-zgs-jgfx') {
        this.zgsJgfxBtnIndex = index
        this.dealZgsJgfxData()
      } else if (type === 'stock-shop-jgfx') {
        this.shopJgfxBtnIndex = index
        this.dealShopJgfxData()
      }
    },
    changeAllType ({btn, index}) {
      if (index === 0) {
        this.showZgs = true
        this.zgsJgfxTabIndex = 0
        this.zgsJgfxBtnIndex = 0
        this.dealZgsJgfxData()
      } else {
        this.showZgs = false
        this.shopJgfxTabIndex = 0
        this.shopJgfxBtnIndex = 0
        this.dealShopJgfxData()
      }
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'stock-zgs-kcfx') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.zgsKcfxTableData)
      } else if (type === 'stock-zgs-jgfx') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.zgsJgfxTableData)
      } else if (type === 'stock-shop-kcfx') {
        tempTable = _.cloneDeep(this.shopKcfxTableData)
      } else if (type === 'stock-shop-jgfx') {
        tempTable = _.cloneDeep(this.shopJgfxTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'stock-zgs-kcfx') {
        this.zgsKcfxTableData.tableHeader = headerList
        this.zgsKcfxTableData.tableContent = tableContent
      } else if (type === 'stock-zgs-jgfx') {
        // 这个来分辨哪个表格需要排序
        this.zgsJgfxTableData.tableHeader = headerList
        this.zgsJgfxTableData.tableContent = tableContent
      } else if (type === 'stock-shop-kcfx') {
        this.shopKcfxTableData.tableHeader = headerList
        this.shopKcfxTableData.tableContent = tableContent
      } else if (type === 'stock-shop-jgfx') {
        this.shopJgfxTableData.tableHeader = headerList
        this.shopJgfxTableData.tableContent = tableContent
      }
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      this.shopqsfxTitle = '趋势分析-' + name
      this.shopjgfxTitle = '结构分析-' + name
      this.getShopQsfxLineData(name)
      this.getShopJgfxBarLineData(name)
    },
    getQsfxLineOption (arr) {
      const option = {
        'legend': {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '(天)',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'axisLabel': {
              'show': true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              }
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          },
          {
            'type': 'value',
            'name': '控制差异率',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '周转天数',
            barWidth: px2px(40),
            itemStyle: {
              barBorderRadius: [px2px(20), px2px(20), 0, 0],
              color: function (params) {
                return '#326DFF'
              }
            },
            data: []
          },
          {
            type: 'line',
            name: '控制差异率',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 1,
            itemStyle: {
              color: '#FFBE14' // 图例的颜色
            },
            lineStyle: {
              color: '#FFBE14',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: '差异率',
            yAxisIndex: 1,
            symbol: 'none',
            itemStyle: {
              color: '#FF0200' // 图例的颜色
            },
            lineStyle: {
              color: '#FF0200',
              type: 'dashed',
              width: px2px(4)
            },
            data: []
          }]
      }
      const labelList = []
      const zztsList = []
      const zzcyList = []
      const cyList = []
      for (let item of arr) {
        labelList.push(item.label)
        zztsList.push(item.zzts)
        zzcyList.push(item.kzcy)
        cyList.push(item.cy)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = zztsList
      option.series[1].data = zzcyList
      option.series[2].data = cyList
      return option
    },
    getJgfxBarLineOption (arr) {
      const option = {
        'legend': {
          show: true,
          top: px2px(20),
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          },
          data: ['库存占比', '销售占比']
        },
        grid: {
          top: px2px(60),
          left: ((this.showZy === true && this.zyJgfxTabIndex === 0) || (this.showZy === false && this.zgsJgfxTabIndex === 1)) ? px2px(100) : px2px(200)
        },
        'xAxis': [
          {
            'type': 'value',
            'name': '',
            interval: 0,
            'show': false,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'category',
            'axisLine': {
              show: false,
              lineStyle: {
                'color': '#ECEFF2'
              }
            },
            axisLabel: {
              show: true,
              textStyle: {
                'color': '#5F6D96',
                fontSize: px2px(28)
              },
              formatter: function (params) {
                let str = ''
                if (params.length > 8) {
                  str = params.substring(0, 8) + '\n' + params.substring(8)
                } else {
                  str = params
                }
                return str
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '库存占比',
            barWidth: px2px(32),
            barGap: 0,
            label: {
              normal: {
                position: 'right',
                formatter: '{c}%',
                'show': true,
                fontSize: px2px(24),
                'color': '#326DFF'
              }
            },
            itemStyle: {
              barBorderRadius: [0, px2px(20), px2px(20), 0],
              color: function (params) {
                return '#326DFF'
              }
            },
            data: []
          },
          {
            type: 'bar',
            name: '销售占比',
            barWidth: px2px(32),
            barGap: 0,
            label: {
              normal: {
                position: 'right',
                formatter: '{c}%',
                show: true,
                fontSize: px2px(24),
                'color': '#D1A226'
              }
            },
            itemStyle: {
              barBorderRadius: [0, px2px(20), px2px(20), 0],
              color: function (params) {
                return '#D1A226'
              }
            },
            data: []
          }]
      }
      const labelList = []
      const kczbList = []
      const xszbList = []
      let i = 0
      for (let item of arr) {
        i++
        labelList.unshift(item.type)
        kczbList.unshift(item.kb)
        xszbList.unshift(item.xb)
        if (i === 10) {
          break
        }
      }
      option.yAxis[0].data = labelList
      option.series[0].data = kczbList
      option.series[1].data = xszbList
      return option
    },
    // 下面是查询数据的方法
    getAllData () {
      this.getZgsKcfxTableData()
      this.zgsqsfxTitle = '趋势分析-' + this.selectedDwName
      this.zgsjgfxTitle = '结构分析-' + this.selectedDwName
      // 查询 子公司趋势分析线图
      this.getZgsQsfxLineData(this.selectedDwName)
      this.getZgsJgfxBarLineData(this.selectedDwName)
      // // 查询子公司 店铺库存分析表格数据
      this.getShopKcfxTableData(this.selectedDwName)
    },
    // 直营子公司库存分析的数据
    async getZgsKcfxTableData () {
      let param = {
        examSub: '直营',
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockCompany.getZgsKcfxTableData,
        param)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zgsKcfxTableDataInit = result
        this.dealzgsKcfxTableData()
      }
    },
    dealzgsKcfxTableData () {
      const list = []
      let dataList = this.zgsKcfxTableDataInit.res
      for (let item of dataList) {
        const obj = {}
        obj.zgs = item.distribution_name
        obj.kzcy = switchPercent(item.diff_rate)
        obj.zzts = formatNumber(initNum(item.zz_rate), 0)
        obj.kzcypm = formatNumber(initNum(item.diff_rate_row), 0)
        obj.zztspm = formatNumber(initNum(item.zz_rate_row), 0)
        list.push(obj)
      }
      if (list.length > 0) {
        this.zgsKcfxTableData.tableContent = list.sort(compareSort('kzcypm', true))
      } else {
        this.zgsKcfxTableData.tableContent = []
      }
    },
    // 获取子公司，趋势分析的线图数据
    async getZgsQsfxLineData (companyName) {
      let params = {
        examSub: '直营',
        subName: companyName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockCompany.getZgsQsfxLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zgsQsfxLineDataInit = result
        this.dealzgsQsfxLineData()
      }
    },
    dealzgsQsfxLineData () {
      const list = []
      let dataList = this.zgsQsfxLineDataInit.res
      for (let item of dataList) {
        const obj = {}
        obj.label = item.period_wid
        obj.kzcy = switchPercent(item.diff_rate)
        obj.zzts = formatNumber(initNum(item.zz_rate).toFixed(0), 0)
        obj.cy = switchPercent(item.target_diff_rate)
        list.push(obj)
      }
      if (list.length > 0) {
        this.zgsQsfxLineData = list
      } else {
        this.zgsQsfxLineData = []
      }
    },
    // 子公司库存结构分析获取数据
    async getZgsJgfxBarLineData (companyName) {
      let params = {
        examSub: '直营',
        subName: companyName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockCompany.getZgsJgfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zgsJgfxTableDataInit = result
        this.dealZgsJgfxData()
      }
    },
    dealZgsJgfxData () {
      this.zgsJgfxIndex = this.zgsJgfxTabIndex.toString() + this.zgsJgfxBtnIndex.toString()
      const BarLineList = []
      const list = []
      let dataList = []
      if (this.zgsJgfxIndex === '00') {
        dataList = this.zgsJgfxTableDataInit.resShopq || []
        this.zgsJgfxTableData.tableHeader = this.shopqTableHeader
      } else if (this.zgsJgfxIndex === '01') {
        dataList = this.zgsJgfxTableDataInit.resShopm || []
        this.zgsJgfxTableData.tableHeader = this.shopmTableHeader
      } else if (this.zgsJgfxIndex === '10') {
        dataList = this.zgsJgfxTableDataInit.resDq || []
        this.zgsJgfxTableData.tableHeader = this.dqTableHeader
      } else if (this.zgsJgfxIndex === '11') {
        dataList = this.zgsJgfxTableDataInit.resDm || []
        this.zgsJgfxTableData.tableHeader = this.dmTableHeader
      } else if (this.zgsJgfxIndex === '20') {
        dataList = this.zgsJgfxTableDataInit.resSpuq || []
        this.zgsJgfxTableData.tableHeader = this.spuqTableHeader
      } else if (this.zgsJgfxIndex === '21') {
        dataList = this.zgsJgfxTableDataInit.resSpum || []
        this.zgsJgfxTableData.tableHeader = this.spumTableHeader
      }
      for (let item of dataList) {
        const obj = {}
        obj.type = item.type_name
        obj.xe = formatNumber(initNum(item.sale), 2)
        obj.ke = formatNumber(initNum(item.inv), 2)
        obj.xb = switchPercent(item.sale_rotio_rate)
        obj.kb = switchPercent(item.inv_rotio_rate)
        obj.pc = switchPercent(item.deviation)
        list.push(obj)
        BarLineList.push(obj)
      }
      this.zgsJgfxBarLineData = BarLineList.sort(compareSort('kb', false))
      this.zgsJgfxTableData.tableContent = list.sort(compareSort('ke', false))
    },
    async getShopKcfxTableData (companyName) {
      let param = {
        examSub: '直营',
        subName: companyName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockCompany.getShopKcfxTableData,
        param)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.shopKcfxTableDataInit = result
        this.dealShopKcfxTableData()
      }
    },
    dealShopKcfxTableData () {
      const list = []
      let dataList = this.shopKcfxTableDataInit.res
      for (let item of dataList) {
        const obj = {}
        obj.shop = item.customer_name
        obj.qty = formatNumber(initNum(item.inv_qty), 2)
        obj.amt = formatNumber(initNum(item.inv_amt), 2)
        obj.kzcy = switchPercent(item.diff_rate)
        obj.zzts = formatNumber(initNum(item.zz_rate), 2)
        obj.tb = switchPercent(item.inv_yoy)
        obj.zb = switchPercent(item.rotio_rate)
        list.push(obj)
      }
      if (list.length > 0) {
        this.shopKcfxTableData.tableContent = list.sort(compareSort('amt', false))
      } else {
        this.shopKcfxTableData.tableContent = []
      }
      // 获取第一条数据，查询下面的线图和店铺表格数据
      if (this.shopKcfxTableData.tableContent.length > 0) {
        const firstName = this.shopKcfxTableData.tableContent[0].shop
        this.shopqsfxTitle = '趋势分析-' + firstName
        this.shopjgfxTitle = '结构分析-' + firstName
        // 这里要设置表格第一行默认选中
        this.$eventBus.$emit('resetSelectedRow', {type: 'stock-shop-kcfx', name: firstName})
        this.getShopQsfxLineData(firstName)
        this.getShopJgfxBarLineData(firstName)
      } else {
        this.shopqsfxTitle = '趋势分析'
        this.shopjgfxTitle = '结构分析'
        this.shopQsfxLineData = []
        this.shopJgfxTableDataInit = []
        this.shopJgfxTableData = []
      }
    },
    // 获取店铺，趋势分析的线图数据
    async getShopQsfxLineData (shopName) {
      let params = {
        examSub: '直营',
        cusName: shopName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockCompany.getShopQsfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.label = item.period_wid
          obj.kzcy = (initNum(item.diff_rate) * 100).toFixed(0)
          obj.zzts = formatNumber(initNum(item.zz_rate).toFixed(2), 0)
          obj.cy = (initNum(item.target_diff_rate) * 100).toFixed(0)
          list.push(obj)
        }
        if (list.length > 0) {
          this.shopQsfxLineData = list
        } else {
          this.shopQsfxLineData = []
        }
      }
    },
    async getShopJgfxBarLineData (shopName) {
      let params = {
        examSub: '直营',
        cusName: shopName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stockCompany.getShopJgfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.shopJgfxTableDataInit = result
        this.dealShopJgfxData()
      }
    },
    dealShopJgfxData () {
      this.shopJgfxIndex = this.shopJgfxTabIndex.toString() + this.shopJgfxBtnIndex.toString()
      const BarLineList = []
      const list = []
      let dataList = []
      if (this.shopJgfxIndex === '00') {
        dataList = this.shopJgfxTableDataInit.resDQ || []
        this.shopJgfxTableData.tableHeader = this.dqTableHeader
      } else if (this.shopJgfxIndex === '01') {
        dataList = this.shopJgfxTableDataInit.resDM || []
        this.shopJgfxTableData.tableHeader = this.dmTableHeader
      } else if (this.shopJgfxIndex === '10') {
        dataList = this.shopJgfxTableDataInit.resSPUQ || []
        this.shopJgfxTableData.tableHeader = this.spuqTableHeader
      } else if (this.shopJgfxIndex === '11') {
        dataList = this.shopJgfxTableDataInit.resSPUM || []
        this.shopJgfxTableData.tableHeader = this.spumTableHeader
      }
      for (let item of dataList) {
        const obj = {}
        obj.type = item.type_name
        obj.xe = formatNumber(initNum(item.sale), 2)
        obj.ke = formatNumber(initNum(item.inv), 2)
        obj.xb = switchPercent(item.sale_rotio_rate)
        obj.kb = switchPercent(item.inv_rotio_rate)
        obj.pc = switchPercent(item.deviation)
        list.push(obj)
        BarLineList.push(obj)
      }
      this.shopJgfxBarLineData = BarLineList.sort(compareSort('kb', false))
      this.shopJgfxTableData.tableContent = list.sort(compareSort('ke', false))
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '库存')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '子公司', URL: '库存'})
    this.getAllData()
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  /**
  watch: {
    dateStr: {
      handler () {
        this.getAllData()
      }
    },
    selectedDwName: {
      handler () {
        this.zgsqsfxTitle = '趋势分析-' + this.selectedDwName
        this.zgsjgfxTitle = '结构分析-' + this.selectedDwName
        // 查询 子公司趋势分析线图
        this.getZgsQsfxLineData(this.selectedDwName)
        this.getZgsJgfxBarLineData(this.selectedDwName)
        // // 查询子公司 店铺库存分析表格数据
        this.getShopKcfxTableData(this.selectedDwName)
      }
    }
  }, **/
  components: {
    pageTitle,
    SwitchBtnList,
    MinTitle,
    BarLineChart,
    TableList,
    TypeList,
    Tabs
  }
}
</script>

